import React, { useRef } from "react";
import { useSelector } from "react-redux";
import { exit, prevStep, refresh } from "../services/socket";
import RefreshIcon from "../refreshIcon/refreshIcon";
import "./game-info.css";
import ThemeSwitch from "../themeSwitch/themeSwitch";
import ExitIcon from "../exitIcon/exitIcon";
import Dialog from "../dialog/dialog";
import BackIcon from "../backIcon/backIcon";
import HelpIcon from "../helpIcon/helpIcon";

const GameInfo = () => {
  const ref = useRef(null);
  const tipsRef = useRef(null);
  const game = useSelector(state => state.game.game);
  const { actionLogin, stage, winner, roomId, users } = game;

  let text = null;
  const loginToSearch = winner || actionLogin;
  const color = users.find(u => u.login === loginToSearch)?.color;
  if (stage === "start") text = ` - выложи куб`;
  if (stage === "turn") text = ` - задай вопрос или запусти поиск`;
  if (stage === "search") text = ` - отвечай`;
  if (stage === "check") text = ` - отвечай`;
  if (stage === "canbe") text = ` - поставь круг`;
  if (winner) text = ` - победил`;

  return (
    <div className="game-info">
      <div className="block-wrapper">
        <h1>Статус:</h1>
        <span>
          <span className={`nickname player-${color}`}>{loginToSearch}</span>
          {text}
        </span>
      </div>
      <div className="buttons">
        <ThemeSwitch />
        <span
          title="Новая игра"
          onClick={() => refresh(roomId)}
          className="icon"
        >
          <RefreshIcon />
        </span>

        <span
          className="icon"
          title="Шаг назад"
          onClick={() => {
            prevStep();
          }}
        >
          <BackIcon />
        </span>
        <span
          title="Дополнительная информация"
          className="icon"
          onClick={() => tipsRef.current.showModal()}
        >
          <HelpIcon />
        </span>
        <span
          title="Выход"
          className="icon"
          onClick={() => ref.current.showModal()}
        >
          <ExitIcon />
        </span>
      </div>
      <Dialog ref={ref}>
        <div className="form-page modal-form-page">
          <p>Выйти из комнаты?</p>
          <div style={{ display: "flex", justifyContent: "space-around" }}>
            <button onClick={() => exit()}>Выйти</button>
            <button onClick={() => ref.current.close()}>Отмена</button>
          </div>
        </div>
      </Dialog>
      <Dialog ref={tipsRef}>
        <div className="tips-modal">
          <h1>Варианты заметок</h1>
          <h2>Один из двух видов местности:</h2>
          <div className="tips-wrapper">
            <ul>
              <li>в лесу или в пустыне</li>
              <li>в лесу или на воде</li>
              <li>в лесум или в болоте</li>
              <li>в лесу или в горах</li>
              <li>в пустыне или на воде</li>
            </ul>
            <ul>
              <li>в пустыне или в болоте</li>
              <li>в пустыне или в горах</li>
              <li>на воде или в болоте</li>
              <li>на воде или в горах</li>
              <li>в болоте или в горах</li>
            </ul>
          </div>
          <h2>Не дальше одной клетки от местности или территории животных:</h2>
          <div className="tips-wrapper">
            <ul>
              <li>не дальше одной клетки от леса</li>
              <li>не дальше одной клетки от пустыни</li>
              <li>не дальше одной клетки от болота</li>
            </ul>
            <ul>
              <li>не дальше одной клетки от гор</li>
              <li>не дальше одной клетки от воды</li>
              <li>не дальше одной клетки от любых животных</li>
            </ul>
          </div>
          <h2>Не дальше двух клеток от территории животных или строения:</h2>
          <div className="tips-wrapper">
            <ul>
              <li>не дальше двух клеток от монумента</li>
              <li>не дальше двух клеток от хижины</li>
            </ul>
            <ul>
              <li>не дальше двух клеток от медведей</li>
              <li>не дальше двух клеток от ягуаров</li>
            </ul>
          </div>
          <h2>Не дальше трех клеток от строения определенного цвета:</h2>
          <div className="tips-wrapper">
            <ul>
              <li>не дальше трех клеток от синего строения</li>
              <li>не дальше трех клеток от белого строения</li>
            </ul>
            <ul>
              <li>не дальше трех клеток от зеленого строения</li>
              <li>не дальше трех клеток от черного строения</li>
            </ul>
          </div>
        </div>
      </Dialog>
    </div>
  );
};

export default GameInfo;
